  width: 50%;
}
.map__block-inner {
  padding-top: 80%;
  position: relative;
}
.map__block-inner iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.map__btn {
  position: absolute;
  z-index: 1;
}
@media (max-width: 1100px) {
  .map__inner {
    flex-direction: column;
  }
  .map__content {
    width: 100%;
    order: 1;
    padding-right: 0;
  }
  .map__block {
    width: unset;
    max-width: 500px;
  }
}
@media (max-width: 1100px) {
  .map__title {
    font-size: 70px;
  }
}

.footer {
  padding: 30px 0;
  background-color: #3D3D3D;
  color: #fff;
  border-radius: 30px 30px 0px 0px;
}
.footer__title {
  font-size: 30px;
  font-weight: 400;
}
.footer__contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 70px;
}
.footer__contacts-item:last-child {
  width: 100%;
  font-size: 24px;
  font-weight: 500;
  margin-top: 10px;
}
.footer__tel {
  font-size: 48px;
  font-weight: 500;
}
.footer__whatsup, .footer__telegram, .footer__instagram {
  font-size: 17px;
  vertical-align: middle;
}
.footer__whatsup::before, .footer__telegram::before, .footer__instagram::before {
  vertical-align: middle;
  display: inline-block;
  margin-right: 10px;
}
.footer__whatsup::before {
  content: url(../images/whatsup-icon.svg);
}
.footer__telegram::before {
  content: url(../images/telegram-icon.svg);
}
.footer__instagram::before {
  content: url(../images/inst-icon.svg);
}
.footer__copy {
  text-align: center;
  font-size: 26px;
  margin-bottom: 50px;
}
.footer__info {
  text-align: center;
  font-size: 11px;
  line-height: 13px;
}
@media (max-width: 1350px) {
  .footer__contacts-item:first-child {
    width: 100%;
    margin-bottom: 20px;
  }
}
@media (max-width: 1110px) {
  .footer__whatsup::before, .footer__telegram::before, .footer__instagram::before {
    transform: scale(0.8);
  }
}
@media (max-width: 600px) {
  .footer__tel {
    font-size: 30px;
  }
  .footer__contacts {
    margin-bottom: 0;
  }
}

.modal {
  transform: translateY(-100%);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.349);
  padding: 80px 20px;
  display: flex;
  transition: transform 0.5s;
  overflow: auto;
  z-index: 2;
}
.modal.open {
  transform: translateY(0%);
}
.modal__inner {
  margin: auto;
  background-color: #fff;
  padding: 50px;
  border-radius: 20px;
  color: #000;
}
.modal__inner ul {
  margin: 0;
  text-align: center;
}
.modal__inner ul li + li {
  margin-top: 15px;
}
.modal__inner ul a {
  display: inline-block;
  width: 100%;
  font-size: 24px;
}